		<a href="javascript:;"><img src="../../static/img/mg4.jpg" /></a>
		<a href="javascript:;"><img src="../../static/img/mg2.jpg" /></a>
		<a href="javascript:;"><img src="../../static/img/mg3.jpg" /></a>
		<a href="javascript:;"><img src="../../static/img/mg5.jpg" /></a>
		<a href="javascript:;"><img src="../../static/img/mg6.jpg" /></a>
	</div>
  </div>
</template>
<script>
import $ from 'jquery'
export default {
  name: 'catdoghelp',
  data () {
    return {
      msg: 'Home'
    }
  },
  mounted () {
    // var self = this
    // var html = ''
    $('#softpicarea').on('click', 'a', function () {
      var src = $(this).find('img').attr('src')
      var img = '<img src= ' + src + ' />'
      $('<div>').addClass('winshow').html(img).appendTo($('body'))
    })
    $('body').on('click', '.winshow', function () {
      $(this).remove()
    })
  },
  methods: {
    showPic () {}
  }
}
</script>




